<template>
  <div id="app">
    <h1>Tarefas</h1>
    <TaskProgress :progress="progress" />
    <NewTask @taskAdded="addTask"></NewTask>
    <TaskGrid
      :tasks="tasks"
      @taskdelete="deleteTask"
      @taskchange="toggleTaskState"
    />
  </div>
  <!--@taskdelete.stop para parar propagação-->
</template>

<script>
import NewTask from "./components/NewTask.vue";
import TaskGrid from "./components/TaskGrid.vue";
import TaskProgress from "./components/TaskProgress.vue";

export default {
  data() {
    return {
      tasks: [
        { name: "Lavar louça", pending: false },
        { name: "Estudar POO", pending: true },
      ],
    };
  },
  components: {
    TaskGrid,
    NewTask,
    TaskProgress,
  },
  computed: {
    progress() {
      const total = this.tasks.length;
      const done = this.tasks.filter((t) => !t.pending).length;
      return Math.round((done / total) * 100) || 0;
    },
  },
  methods: {
    addTask(task) {
      //this.task.push(task) -> inserir task do parâmetro no vetor
      const sameName = (t) => t.name === task.name;
      //filtrando o vetor tasks
      const reallyNew = this.tasks.filter(sameName);
      if (reallyNew.length == 0) {
        this.tasks.push({
          name: task.name,
          //definindo a pendência
          pending: task.pending || true,
        });
      }
    },
    //Função recebe i, pois é o que é passado em NewR=Task
    deleteTask(i) {
      if (i >= 0) {
        this.tasks.splice(i, 1);
      }
    },
    toggleTaskState(i) {
      this.tasks[i].pending = !this.tasks[i].pending;
    },
  },
  watch: {
    //passando a variável tasks para o local storage e transformando em um arquivo JSON
    //tasks(){
    //  localStorage.setItem('tasks', JSON.stringify(this.tasks));
    //}
    tasks: {
      deep: true, 
      handler(){
        localStorage.setItem('tasks', JSON.stringify(this.tasks));
      }
    }

  },
  //método de ciclo de vida
  created() {
    const json = localStorage.getItem('tasks');
    const array = JSON.parse(json);
    //Verificando se o parâmetro é um array, caso contrário retorna um array vazio
    this.tasks = Array.isArray(array) ? array : [];
  }
};
</script>

