<template>
  <div class="class task-grid">
    <template v-if="tasks.length">
      <Task
        v-for="(task, i) in tasks"
        :key="task.name"
        :task="task"
        @taskdelete="$emit('taskdelete', i)"
        @taskchange="$emit('taskchange', i)"
        >{{ task.name }}</Task
      >
    </template>
    <p v-else class="no-task">Sua vida está resolvida!</p>
  </div>
</template>

<script>
import Task from "./Task.vue";

export default {
  props: {
    tasks: {
      type: Array,
      required: true,
    },
  },
  components: {
    Task,
  },
};
</script>

<style>
.task-grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.task-grid .task {
  margin: 10px;
}

.no-task {
  color: #AAA;
  font-size: 1.7rem;
}
</style>