<template>
  <div class="new-task">
    <!--Criando um v-model no input para o que for digitado ser armazenado em name-->
    <!--@keydown.enter = apertar o enter-->
    <input
      v-model="name"
      type="text"
      class="form-element"
      placeholder="Nova tarefa"
      @keydown.enter="add"
    />
    <button @click="add" class="form-element">+</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: "",
    };
  },
  methods: {
    //emit: serve para o componente filho se comunicar com o pai
    add() {
      //taskAddeds: nome do evento
      this.$emit("taskAdded", { name: this.name });
      this.name = "";
    },
  },
};
</script>

<style>
.new-task {
  margin: 35px;
}

.form-element {
  outline: none;
  font-size: 2rem;
  border: 1px solid #FFF;
  padding: 5px 10px 8px;
  color: white;
}

input.form-element {
  background: #FFF2;
  width: 500px;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
}

button.form-element {
  border-left: none;
  background-color: #2196F3;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}
</style>