<template>
  <div @click="$emit('taskchange', task)" class="task" :class="stateClass">
    <span class="close" @click.stop="$emit('taskdelete', task)"> x </span>
    <p>{{ task.name }}</p>
  </div>
</template>

<script>
export default {
  props: {
    task: {
      //recebe um objeto
      type: Object,
      required: true,
    },
  },
  computed: {
    stateClass() {
      return {
        //returna a classe pendening se a task estiver pendente
        pending: this.task.pending,
        //retorna a cçasse done se a task estiver pronta
        done: !this.task.pending,
      };
    },
  },
};
</script>

<style>
.task {
  position: relative;
  box-sizing: border-box;
  width: 350px;
  height: 150px;
  padding: 10px;
  border-radius: 8px;
  font-size: 2rem;
  font-weight: 300;
  /* maozinha de link */
  cursor: pointer;
  /* não selecionar o texto */
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pending {
  border-left: 12px solid #B73229;
  background-color: #F44336;
}

.done {
  color: #DDD;
  border-left: 12px solid #0A8F08;
  background-color: #4CAF50;
  text-decoration: line-through;
}

.close {
  position: absolute;
  right: 10px;
  font-size: 0.9rem;
  top: 10px;
  height: 20px;
  width: 20px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  font-weight: 600px;
}

.pending .close {
  background-color: #873229;
}

.done .close {
  background-color: #0A8F08;
}
</style>
